               class="d-block w-100"
          >
        </template>
      </b-carousel-slide>
    </b-carousel>
    <b-container class="main1">
      <div class="border-top shadow-sm mb-2 d-flex flex-row loose-top">
        <h2 class="text-dark">Deals of the Day</h2>
        <b-button class="ml-auto shadow popup" pill variant="" style="background-color: #5FACBE;" to="/flash-sale">Show more</b-button>
      </div>
      <div style="border: 0px solid black; background-color: white;">
        <product-scroll-section :products="flashSaleProducts"></product-scroll-section>
      </div>
      <br>
      <b-row>
        <b-col cols="12" md="6">
          <div class="border mb-2 loose-top">
            <b-row>
              <b-col>
                <h2 class="text-dark">Categories</h2>
              </b-col>
            </b-row>
            <b-row class="">
                <b-col v-for="(category,id) in categories" :key="id" cols="6" class="mb-2">
                  <category-box :category="category"></category-box>
                </b-col>
            </b-row>
            <b-row>
                <b-button class="ml-auto mt-2shadow mr-3 popup" pill variant="" style="background-color: #5FACBE;" to="/new-arrivals">Show more</b-button>
            </b-row>
          </div>
        </b-col>
        <b-col cols="12" md="6">
          <div class="border mb-2 loose-top">
            <b-row>
              <b-col>
                <h2 class="text-dark">New Arrivals</h2>
              </b-col>
            </b-row>
            <b-row>
              <b-col cols="6"  v-for="(product,id) in newArrivalsProducts" :key="id" class="mb-2">
                <product-box :product="product"></product-box>
              </b-col>
            </b-row>
            <b-row>
                <b-button class="ml-auto shadow mr-3 mt-2 popup" pill variant="" style="background-color: #5FACBE; border: 0;" to="/new-arrivals">Show more</b-button>
