</template>

<script>
import ProductScrollSection from '../components/ProductScrollSection.vue'
import loader from '../mixins/loader'
import ProductBox from '../components/Boxes/ProductBox'
import CategoryBox from '../components/Boxes/CategoryBox'
export default {
  data () {
    return {
      flashSaleProducts: null,
      topRatedProducts: null,
      mostSoldProducts: null,
      newArrivalsProducts: null,
      ourShopProducts: null,
      sliding: false,
      images: [],
      categories: []
    }
  },
  components: {
    ProductScrollSection,
    ProductBox,
    CategoryBox
  },
  mixins: [
    loader
  ],
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    loadFlashSale () {
      const offset = 0
      const limit = 5
      this.loader('/products/flash-sale', { offset, limit }, 'flash-sale-home')
        .then(response => {
          this.flashSaleProducts = response.data.products
          while (this.flashSaleProducts.length < limit) {
            this.flashSaleProducts.push(this.flashSaleProducts[0])
          }
        })
    },
    loadTopRated () {
      const offset = 0
      const limit = 5
      this.loader('/products/top-rated', { offset, limit }, 'top-rated-home')
        .then(response => {
          this.topRatedProducts = response.data.products
          while (this.topRatedProducts.length < limit) {
            this.topRatedProducts.push(this.topRatedProducts[0])
          }
        })
    },
    loadMostSold () {
      const offset = 0
      const limit = 5
      this.loader('/products/most-sold', { offset, limit }, 'most-sold-home')
        .then(response => {
          this.mostSoldProducts = response.data.products
          while (this.mostSoldProducts.length < limit) {
            this.mostSoldProducts.push(this.mostSoldProducts[0])
          }
        })
    },
    loadNewArrivals () {
      const offset = 0
      const limit = 4
      this.loader('/products/new-arrivals', { offset, limit }, 'new-arrivals-home')
        .then(response => {
          this.newArrivalsProducts = response.data.products
          while (this.newArrivalsProducts.length < limit) {
            this.newArrivalsProducts.push(this.newArrivalsProducts[0])
          }
        })
    },
    loadOurShop () {
      const offset = 0
      const limit = 5
      this.loader('/products/our-shop', { offset, limit }, 'our-shop-home')
        .then(response => {
          this.ourShopProducts = response.data.products
          while (this.ourShopProducts.length < limit) {
            this.ourShopProducts.push(this.ourShopProducts[0])
          }
        })
    },
    loadSlideImages () {
      this.loader('/banner/all', null, 'banner-home')
        .then(response => {
          this.images = response.data
          console.log(response)
        })
    },
    loadCategories () {
      const offset = 0
      const limit = 4
      this.loader('/categories/all', { offset, limit }, 'categories-home')
        .then(response => {
          this.categories = response.data.categories
        })
    },
    loadAll () {
      this.loadFlashSale()
      this.loadTopRated()
      this.loadNewArrivals()
      this.loadMostSold()
      this.loadOurShop()
      this.loadCategories()
      this.loadSlideImages()
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadAll()
    }
  },
  created () {
    this.loadAll()
  }
}
</script>

