<template>
  <div id="holder" @click="clicking" :class="{ shadow }" @mouseenter="shadow = true" @mouseleave="shadow = false">
    <b-overlay :show="busy" opacity="0.4" spinner-small spinner-variant="primary" class="" @hidden="onHidden">
      <b-card :img-src="imgSrc" img-width="250px" img-height="200px" :img-alt="category.name" img-top>
        <b-card-text style="padding: 0 5%;">
          <div class="text-truncate">
            {{category.name}}
          </div>
        </b-card-text>
      </b-card>
    </b-overlay>
  </div>
</template>

<script>
export default {
  name: 'CategoryBox',
  data () {
    return {
      busy: false,
      shadow: false,
      images: this.category.images
    }
  },
  props: [
    'category'
  ],
  computed: {
    imgSrc () {
      if (this.category.images) {
        return this.category.images[0].link
      } else if (this.category.image) {
        return this.category.image.link
      }
      return ''
    }
  },
  methods: {
    onHidden () {
      this.$refs.button.focus()
    },
    clicking () {
      this.busy = true
      this.$router.push({ name: 'CertainCategory', query: { 'cat-name': this.category.name } })
    }
  }
}
</script>

<style scoped>
#holder {
  cursor: pointer;
  padding: 0;
  text-align: center;
}
.card-body { /* this is needed */
  padding: 0;
}
</style>
